@import '../../style.scss';

.post {
    @include themify($themes) {
        box-shadow: 0px 0px 25px -10px rgba(0, 0, 0, 0.38);
        border-radius: 20px;
        background-color: themed("bg");
        color: themed("textColor");


        .container {
            padding: 20px;

            .user {
                display: flex;
                align-items: center;
                justify-content: space-between;

                .userInfo {
                    display: flex;
                    gap: 20px;

                    img {
                        width: 40px;
                        height: 40px;
                        border-radius: 50%;
                        object-fit: cover;

                    }

                    .details {
                        display: flex;
                        flex-direction: column;

                        .name {
                            font-weight: 500;

                        }

                        .date {
                            font-size: 20px;

                        }
                    }


                }


            }

            .content {
                margin: 20px 0px;
                p {}

                img {
                    width: 100%;
                    max-height: 500px;
                    object-fit: cover;
                    margin-top: 20px;
                }

            }

            .info {
                display: flex;
                gap: 20px;
                
                .item {
                    display: flex;
                    align-items: center;
                    gap: 10px;
                    cursor: pointer;
                }
            }

        }

    }
}